import React,{useEffect, useState} from 'react'
import TaxShow from './TaxShow'
import TaxForm from './TaxForm'
import Spinner from '../../../UI/Spinner'
const TaxInfoIndex = (props) => {
    //options-> pending , done , inReview
    const [status,setStatus] = useState('')
    useEffect(()=>{
        setStatus('pending')
    },[])
    if(status===''){
        return(
            <Spinner />
        );
    }
    if(status==='pending'){
        return(
            <TaxForm {...props} />
        );
    }
    if(status==='done' || status==='inReview'){
        return(
            <TaxShow />
        );
    }
    
}
export default TaxInfoIndex