import React,{useEffect, useState} from 'react'
import  ShowCompanyDetails from './ShowCompanyDetails'
import FormCompanyDetails from './FormCompanyDetails'
import Spinner from '../../../UI/Spinner'
const BasicInfoIndex = (props) => {
    //options-> pending , done , inReview
    const [status,setStatus] = useState('')
    useEffect(()=>{
        setStatus('pending')
    },[])
    if(status===''){
        return(
            <Spinner />
        );
    }
    if(status==='pending'){
        return(
            <FormCompanyDetails {...props} />
        );
    }
    if(status==='done' || status==='inReview'){
        return(
            <ShowCompanyDetails />
        );
    }
    
}
export default BasicInfoIndex